import cmedia from './Post.module.css'


const Post = (props) => {
    return (
        <div className={cmedia.item}>
            <img alt="img1" src="data:image/jpeg;base64," />
            <span>{props.itext}</span>
            <div>
                <span>{props.likes} likes</span>
            </div>
        </div>
    )
}
export default Post;